
/*--------------------------------------------------- 
		Waits for images to load and appends a class
---------------------------------------------------
 */

(function() {
  define("util/wait-image", ["imagesloaded/imagesloaded"], function(ImagesLoaded) {
    var WaitImage;
    return WaitImage = (function() {
      WaitImage.prototype.element = null;

      function WaitImage(element) {
        this.element = element;
        new ImagesLoaded(this.element[0], (function(_this) {
          return function() {
            var loadClass;
            loadClass = _this.element.data('wait-image');
            return _this.element.addClass(loadClass);
          };
        })(this));
      }

      WaitImage.waitAll = function() {
        var waitElements;
        waitElements = $('*[data-wait-image]');
        return waitElements.each(function() {
          return new WaitImage($(this));
        });
      };

      return WaitImage;

    })();
  });

}).call(this);

