
/*--------------------------------------------------- 
		Our app entrypoint which crawls the DOM for components
---------------------------------------------------
 */

(function() {
  define('main',[], function() {
    var components;
    require(["common"]);
    components = $('*[data-component]');
    return components.each(function() {
      var component;
      component = $(this).attr('data-component');
      return require([component], (function(_this) {
        return function(compClass) {
          return _this.component = new compClass($(_this));
        };
      })(this));
    });
  });

  require(["main"]);

}).call(this);

