
/*--------------------------------------------------- 
		Generic expandable list with 'Read more' link
---------------------------------------------------
 */

(function() {
  define("component/expandable-list", [], function() {
    var ExpandableList;
    return ExpandableList = (function() {
      ExpandableList.prototype.element = null;

      function ExpandableList(element) {
        var collapsedClass, collapsedElement, itemClass, itemElement, limit, moreClass, moreElement;
        this.element = element;
        limit = parseInt(this.element.data('expandable-limit'));
        itemClass = this.element.data('expandable-item');
        collapsedClass = this.element.data('expandable-collapse');
        moreClass = this.element.data('expandable-more');
        itemElement = $("." + itemClass, this.element);
        collapsedElement = $("." + collapsedClass, this.element);
        moreElement = $("." + moreClass, this.element);
        if (itemElement.length <= limit) {
          moreElement.hide();
          return;
        }
        if (collapsedElement.length === 0 || moreElement.length === 0) {
          return;
        }
        moreElement.on('click', function() {
          moreElement.hide();
          return collapsedElement.removeClass(collapsedClass);
        });
      }

      return ExpandableList;

    })();
  });

}).call(this);

