
/*--------------------------------------------------- 
		Fixed contact-us widget
---------------------------------------------------
 */

(function() {
  define("component/contact-us", [], function() {
    var ContactUs;
    return ContactUs = (function() {
      ContactUs.prototype.element = null;

      ContactUs.prototype.scrollDiv = null;

      ContactUs.prototype.offset = 0;

      function ContactUs(element) {
        this.element = element;
        this.scrollDiv = $('.layout__content');
        this.registerEvents();
        this.layout();
        this.scrollDiv.scroll((function(_this) {
          return function() {
            return _this.layout();
          };
        })(this));
        $(window).resize((function(_this) {
          return function() {
            return _this.layout();
          };
        })(this));
        this.element.addClass('contact-us--positioned');
      }

      ContactUs.prototype.layout = function() {
        this.offset = this.obtainPositionOffset();
        return this.updatePosition();
      };

      ContactUs.prototype.obtainPositionOffset = function() {
        var offset;
        offset = 0;
        offset += $('.top-bar').outerHeight();
        offset += $('.header--small').outerHeight();
        offset += $('.hero-banner').outerHeight();
        offset += $('.sub-navigation').outerHeight();
        return offset;
      };

      ContactUs.prototype.updatePosition = function() {
        var offset, scrollTop;
        scrollTop = this.scrollDiv.scrollTop();
        offset = Math.max(scrollTop, this.offset);
        return this.element.css('top', offset);
      };

      ContactUs.prototype.registerEvents = function() {
        $('.contact-us__item', this.element).on('click', (function(_this) {
          return function(e) {
            e.stopPropagation();
            return $(e.delegateTarget).addClass('contact-us__item--open');
          };
        })(this));
        return $('html').on('click', (function(_this) {
          return function() {
            return $('.contact-us__item--open').removeClass('contact-us__item--open');
          };
        })(this));
      };

      return ContactUs;

    })();
  });

}).call(this);

