
/*--------------------------------------------------- 
		Waits elements to be shown on the page
---------------------------------------------------
 */

(function() {
  define("util/wait-visible", ["waypoints", "waypoints.inview"], function() {
    var WaitVisible;
    return WaitVisible = (function() {
      WaitVisible.prototype.element = null;

      function WaitVisible(element) {
        var waypoint;
        this.element = element;
        waypoint = new Waypoint.Inview({
          element: this.element[0],
          entered: (function(_this) {
            return function(direction) {
              var visibleClass;
              visibleClass = _this.element.data('wait-visible');
              return _this.element.addClass(visibleClass);
            };
          })(this)
        });
      }

      WaitVisible.waitAll = function() {
        var waitElements;
        waitElements = $('*[data-wait-visible]');
        waitElements.each(function() {
          return new WaitVisible($(this));
        });
        return $('.layout__content').scroll(function() {
          return Waypoint.refreshAll();
        });
      };

      return WaitVisible;

    })();
  });

}).call(this);

