
/*--------------------------------------------------- 
		Handles custom wysiwyg layout
---------------------------------------------------
 */

(function() {
  define("util/wysiwyg", [], function() {
    var Wysiwyg;
    return Wysiwyg = (function() {
      Wysiwyg.prototype.element = null;

      function Wysiwyg(element) {
        this.element = element;
        this.splitHeadings();
      }

      Wysiwyg.prototype.splitHeadings = function() {
        var currentWysiwyg, elementTag, headerTag, nextWysiwyg, _results;
        elementTag = "<article class='" + (this.element.attr('class')) + "'></article>";
        currentWysiwyg = this.element;
        _results = [];
        while (true) {
          headerTag = $('h4', currentWysiwyg).eq(0);
          if (headerTag.length === 0) {
            break;
          }
          nextWysiwyg = $(elementTag);
          currentWysiwyg.after(nextWysiwyg);
          nextWysiwyg.append(headerTag.nextAll());
          headerTag.addClass('wysiwyg-split-header');
          nextWysiwyg.before(headerTag);
          if (currentWysiwyg.children().length === 0) {
            currentWysiwyg.remove();
          }
          _results.push(currentWysiwyg = nextWysiwyg);
        }
        return _results;
      };

      Wysiwyg.parseAll = function() {
        var wysiwygElements;
        wysiwygElements = $('.wysiwyg');
        return wysiwygElements.each(function() {
          return new Wysiwyg($(this));
        });
      };

      return Wysiwyg;

    })();
  });

}).call(this);

