
/*--------------------------------------------------- 
		Controls the custom two-image carousel on the homepage
---------------------------------------------------
 */

(function() {
  define("component/homepage-carousel", ["requestAnimationFrame/requestAnimationFrame"], function(requestAnimationFrame) {
    var HomepageCarousel;
    return HomepageCarousel = (function() {
      HomepageCarousel.prototype.element = null;

      HomepageCarousel.prototype.carousel = null;

      HomepageCarousel.prototype.carouselHeight = 0;

      HomepageCarousel.prototype.images = null;

      HomepageCarousel.prototype.imageBackgrounds = null;

      HomepageCarousel.prototype.oldImage = null;

      HomepageCarousel.prototype.oldBackgroundImage = null;

      HomepageCarousel.prototype.currentImage = null;

      HomepageCarousel.prototype.currentBackgroundImage = null;

      HomepageCarousel.prototype.currentIndex = -1;

      HomepageCarousel.prototype.isAnimating = false;

      HomepageCarousel.TRANSITION_DURATION = 600;

      HomepageCarousel.BACKGROUND_DELAY = 100;

      HomepageCarousel.OVERLAY_DELAY = 400;

      function HomepageCarousel(element) {
        var carousel, slides;
        this.element = element;
        if ($('.ie9').length > 0) {
          HomepageCarousel.TRANSITION_DURATION = 20;
          HomepageCarousel.BACKGROUND_DELAY = 20;
          HomepageCarousel.OVERLAY_DELAY = 20;
        }
        this.extractImages();
        this.loadBackgrounds();
        this.registerEvents();
        carousel = null;
        slides = $('.homepage-carousel__slides', this.element);
        slides.owlCarousel({
          items: 1,
          autoHeight: true,
          nav: true,
          navText: ['', ''],
          smartSpeed: HomepageCarousel.TRANSITION_DURATION + HomepageCarousel.BACKGROUND_DELAY,
          responsiveRefreshRate: 20,
          onInitialize: function() {
            return carousel = this;
          },
          onInitialized: (function(_this) {
            return function() {
              return _this.carousel = carousel;
            };
          })(this),
          onResize: (function(_this) {
            return function() {
              return _this.carousel.trigger('changed', {
                property: {
                  name: 'position',
                  value: _this.carousel._current
                }
              });
            };
          })(this),
          onResized: (function(_this) {
            return function() {
              return _this.carouselHeight = _this.element.outerHeight();
            };
          })(this),
          onDrag: (function(_this) {
            return function() {
              _this.element.addClass('homepage-carousel--dragging');
              return _this.beginAnimation();
            };
          })(this),
          onDragged: (function(_this) {
            return function() {
              _this.element.removeClass('homepage-carousel--dragging');
              return _this.endAnimation();
            };
          })(this),
          onChanged: (function(_this) {
            return function() {
              var index;
              index = carousel.normalize(carousel.current());
              if ((index != null) && index !== _this.currentIndex) {
                return _this.slideChanged(index);
              }
            };
          })(this)
        });
      }

      HomepageCarousel.prototype.extractImages = function() {
        this.images = [];
        return $('.homepage-carousel__image', this.element).each((function(_this) {
          return function(i, val) {
            var image;
            image = $(val).detach();
            return _this.images.push(image);
          };
        })(this));
      };

      HomepageCarousel.prototype.loadBackgrounds = function() {
        this.imageBackgrounds = [];
        return $('.homepage-carousel__figure', this.element).each((function(_this) {
          return function(i, val) {
            var $image, background, image;
            background = $(val).data('background');
            image = new Image();
            image.src = background;
            $image = $('<div class="homepage-carousel__bgimage"></div>');
            $image.css('background-image', "url(" + background + ")");
            return _this.imageBackgrounds.push($image);
          };
        })(this));
      };

      HomepageCarousel.prototype.registerEvents = function() {};

      HomepageCarousel.prototype.slideChanged = function(index) {
        var first, last;
        first = index === 0;
        last = index === this.images.length - 1;
        if (first) {
          $('.owl-prev', this.element).addClass('disabled');
        } else {
          $('.owl-prev', this.element).removeClass('disabled');
        }
        if (last) {
          $('.owl-next', this.element).addClass('disabled');
        } else {
          $('.owl-next', this.element).removeClass('disabled');
        }
        return this.loadImage(index);
      };

      HomepageCarousel.prototype.loadImage = function(index) {
        var image, imageBackground;
        if (index === this.currentIndex) {
          return;
        }
        this.oldImage = this.currentImage;
        this.oldBackgroundImage = this.currentBackgroundImage;
        this.currentIndex = index;
        image = this.images[index];
        image.appendTo(this.element);
        image.removeClass('homepage-carousel__image--hide');
        image.css('opacity', '');
        imageBackground = this.imageBackgrounds[index];
        imageBackground.prependTo(this.element);
        this.currentImage = null;
        this.currentBackgroundImage = null;
        this.endAnimation();
        setTimeout((function(_this) {
          return function() {
            return imageBackground.addClass('homepage-carousel__bgimage--show');
          };
        })(this), HomepageCarousel.BACKGROUND_DELAY);
        setTimeout((function(_this) {
          return function() {
            return image.addClass('homepage-carousel__image--show');
          };
        })(this), HomepageCarousel.OVERLAY_DELAY);
        this.currentImage = image;
        return this.currentBackgroundImage = imageBackground;
      };

      HomepageCarousel.prototype.beginAnimation = function() {
        this.isAnimating = true;
        return this.tickAnimation();
      };

      HomepageCarousel.prototype.tickAnimation = function() {
        var progress;
        if (!this.isAnimating || (this.currentImage == null)) {
          return;
        }
        progress = Math.abs(this.carousel.drag.distance) / ($(window).width() * 0.8);
        this.currentImage.css('opacity', Math.max(1 - (progress * 2), 0));
        return requestAnimationFrame((function(_this) {
          return function() {
            return _this.tickAnimation();
          };
        })(this));
      };

      HomepageCarousel.prototype.endAnimation = function() {
        var image, imageBackground;
        this.isAnimating = false;
        if (this.oldImage != null) {
          image = this.oldImage;
          image.removeClass('homepage-carousel__image--show');
          image.addClass('homepage-carousel__image--hide');
          image.css('opacity', '');
          setTimeout((function(_this) {
            return function() {
              return image.detach();
            };
          })(this), HomepageCarousel.TRANSITION_DURATION);
          this.oldImage = null;
        }
        if (this.oldBackgroundImage != null) {
          imageBackground = this.oldBackgroundImage;
          setTimeout((function(_this) {
            return function() {
              return imageBackground.removeClass('homepage-carousel__bgimage--show');
            };
          })(this), HomepageCarousel.BACKGROUND_DELAY);
          setTimeout((function(_this) {
            return function() {
              return imageBackground.detach();
            };
          })(this), HomepageCarousel.BACKGROUND_DELAY + HomepageCarousel.TRANSITION_DURATION);
          this.oldBackgroundImage = null;
        }
        if (this.currentImage != null) {
          this.currentImage.addClass('homepage-carousel__image--show');
          return this.currentImage.css('opacity', '');
        }
      };

      return HomepageCarousel;

    })();
  });

}).call(this);

