
/*--------------------------------------------------- 
		Used to control the mobile navigation
---------------------------------------------------
 */

(function() {
  define("component/mobile-nav", ["nanoscroller/jquery.nanoscroller"], function() {
    var MobileNav;
    return MobileNav = (function() {
      MobileNav.prototype.element = null;

      MobileNav.prototype.layout = null;

      MobileNav.prototype.isTransitioning = false;

      MobileNav.prototype.lastHeight = 0;

      MobileNav.TRANSITION_DURATION = 200;

      MobileNav.COLLAPSE_DELAY = 150;

      MobileNav.EXPAND_DELAY = 150;

      function MobileNav(element) {
        this.element = element;
        this.layout = $('.layout');
        $('.scrollable', this.element).nanoScroller({
          paneClass: 'scrollable__pane',
          sliderClass: 'scrollable__slider',
          contentClass: 'scrollable__content'
        });
        this.registerEvents();
      }

      MobileNav.prototype.registerEvents = function() {
        $('.mobile-nav__search, .mobile-nav__search-close').on('click', (function(_this) {
          return function(e) {
            e.preventDefault();
            return _this.toggleSearchMenu();
          };
        })(this));
        $('.mobile-nav__close', this.element).on('click', (function(_this) {
          return function(e) {
            e.preventDefault();
            return _this.toggleMenu();
          };
        })(this));
        $('.mobile-nav__link', this.element).on('click', (function(_this) {
          return function(e) {
            var item;
            item = $(e.delegateTarget).closest('.mobile-nav__item');
            if ($('.mobile-nav__items', item).length > 0) {
              e.preventDefault();
              return _this.toggleNestedMenu(item);
            }
          };
        })(this));
        return $('.header__hamburger').on('click', (function(_this) {
          return function(e) {
            e.preventDefault();
            return _this.toggleMenu();
          };
        })(this));
      };

      MobileNav.prototype.toggleMenu = function() {
        var hamburgerLayer;
        if (this.isTransitioning) {
          return;
        }
        hamburgerLayer = $('header__hamburger .hamburger__layer');
        if (this.layout.hasClass('layout--pushed')) {
          return this.layout.removeClass('layout--pushed');
        } else {
          this.resetMenu();
          return this.layout.addClass('layout--pushed');
        }
      };

      MobileNav.prototype.toggleSearchMenu = function() {
        var header, searchText;
        if (this.isTransitioning) {
          return;
        }
        header = $('.mobile-nav__header', this.element);
        header.toggleClass('mobile-nav__header--searching');
        if (header.hasClass('mobile-nav__header--searching')) {
          searchText = $('.search__text', this.element);
          searchText.val('');
          return searchText.focus();
        }
      };

      MobileNav.prototype.toggleNestedMenu = function(item, transitionDuration) {
        var excluded, itemParent, listParent, siblings;
        if (transitionDuration == null) {
          transitionDuration = MobileNav.TRANSITION_DURATION;
        }
        if (this.isTransitioning) {
          return;
        }
        itemParent = item.parents('.mobile-nav__item--open').find('> .mobile-nav__element');
        listParent = item.closest('.mobile-nav__items');
        siblings = $('> .mobile-nav__item', listParent).filter(function() {
          return $(this)[0] !== item[0];
        });
        excluded = itemParent.add(siblings);
        this.isTransitioning = true;
        if (!item.hasClass('mobile-nav__item--open')) {
          item.addClass('mobile-nav__item--open');
          $('> .mobile-nav__items', item).slideDown(transitionDuration);
          excluded.filter('.mobile-nav__element').addClass('mobile-nav__element--hide');
          excluded.filter('.mobile-nav__item').addClass('mobile-nav__item--hide');
          return setTimeout((function(_this) {
            return function() {
              _this.lastHeight = excluded.outerHeight();
              return excluded.stop(true).animate({
                height: 0
              }, transitionDuration, function() {
                return _this.isTransitioning = false;
              });
            };
          })(this), MobileNav.COLLAPSE_DELAY);
        } else {
          $('> .mobile-nav__items', item).slideUp();
          setTimeout((function(_this) {
            return function() {
              return excluded.each(function(i, val) {
                var excludedItem;
                excludedItem = $(val);
                return excludedItem.stop(true).animate({
                  height: _this.lastHeight
                }, transitionDuration, function() {
                  return $(this).css('height', '');
                });
              });
            };
          })(this), MobileNav.EXPAND_DELAY);
          return setTimeout((function(_this) {
            return function() {
              _this.isTransitioning = false;
              item.removeClass('mobile-nav__item--open');
              excluded.removeClass('mobile-nav__element--hide');
              return excluded.removeClass('mobile-nav__item--hide');
            };
          })(this), transitionDuration + MobileNav.EXPAND_DELAY);
        }
      };

      MobileNav.prototype.resetMenu = function() {
        $('*', this.element).css('height', '');
        $('.mobile-nav__item--open', this.element).each(function(i, val) {
          return $('> .mobile-nav__items', this).slideUp(0);
        });
        $('.mobile-nav__item--open', this.element).removeClass('mobile-nav__item--open');
        $('.mobile-nav__item--hide', this.element).removeClass('mobile-nav__item--hide');
        return $('.mobile-nav__element--hide', this.element).removeClass('mobile-nav__element--hide');
      };

      MobileNav.prototype.hasLink = function(link) {
        return $(".mobile-nav__link[href='" + link + "']", this.element).length > 0;
      };

      MobileNav.prototype.navigateTo = function(link) {
        var candidates, item, parents;
        this.toggleMenu();
        candidates = $(".mobile-nav__link[href='" + link + "']", this.element).closest('.mobile-nav__item');
        candidates = candidates.filter(function() {
          return $(this).find('> .mobile-nav__items').length > 0;
        });
        item = candidates.eq(0);
        this.toggleNestedMenu(item, 0);
        parents = item.parents('.mobile-nav__item');
        $('> .mobile-nav__items', parents).css('display', 'block');
        $('> .mobile-nav__element', parents).css('height', '0px');
        parents.siblings().css('height', '0px');
        return parents.addClass('mobile-nav__item--open');
      };

      return MobileNav;

    })();
  });

}).call(this);

