
/*--------------------------------------------------- 
		Standard image carousel
---------------------------------------------------
 */

(function() {
  define("component/feature-images", ["imagesloaded/imagesloaded"], function(ImagesLoaded) {
    var FeatureImages;
    return FeatureImages = (function() {
      FeatureImages.prototype.element = null;

      function FeatureImages(element) {
        this.element = element;
        new ImagesLoaded(this.element[0], (function(_this) {
          return function() {
            return _this.loadCarousel();
          };
        })(this));
      }

      FeatureImages.prototype.loadCarousel = function() {
        var slides;
        slides = $('.feature-images__items', this.element);
        return slides.owlCarousel({
          items: 1,
          loop: true,
          autoHeight: true,
          slideBy: 'page',
          nav: true,
          navText: ['', ''],
          onResize: function() {
            return this.trigger('changed', {
              property: {
                name: 'position',
                value: this._current
              }
            });
          }
        });
      };

      return FeatureImages;

    })();
  });

}).call(this);

