
/*--------------------------------------------------- 
		Generic video CTA
---------------------------------------------------
 */

(function() {
  define("component/video-cta", ["magnific-popup/jquery.magnific-popup"], function(MagnificPopup) {
    var VideoCTA;
    return VideoCTA = (function() {
      VideoCTA.prototype.element = null;

      VideoCTA.prototype.href = null;

      VideoCTA.DISABLE_BREAKPOINT = 700;

      function VideoCTA(element) {
        var vimeo, youtube;
        this.element = element;
        vimeo = this.element.data('vimeo');
        youtube = this.element.data('youtube');
        if (vimeo != null) {
          this.loadVimeo(vimeo);
        } else if (youtube != null) {
          this.loadYoutube(youtube);
        }
        if ((vimeo != null) || (youtube != null)) {
          this.element.on('click', (function(_this) {
            return function(e) {
              if ($(window).width() <= VideoCTA.DISABLE_BREAKPOINT) {
                return window.location.href = _this.href;
              }
            };
          })(this));
        }
      }

      VideoCTA.prototype.loadVimeo = function(id) {
        this.href = "//player.vimeo.com/video/" + id;
        this.element.magnificPopup({
          type: 'iframe',
          items: {
            src: this.href
          },
          disableOn: VideoCTA.DISABLE_BREAKPOINT,
          mainClass: 'mfp-fade',
          removalDelay: 160,
          preloader: false,
          fixedContentPos: false
        });
        return this.element.addClass('cta--video-ready');
      };

      VideoCTA.prototype.loadYoutube = function(id) {
        this.href = "//www.youtube.com/watch?v=" + id;
        this.element.magnificPopup({
          type: 'iframe',
          items: {
            src: this.href
          },
          disableOn: VideoCTA.DISABLE_BREAKPOINT,
          mainClass: 'mfp-fade',
          removalDelay: 160,
          preloader: false,
          fixedContentPos: false
        });
        return this.element.addClass('cta--video-ready');
      };

      return VideoCTA;

    })();
  });

}).call(this);

