
/*--------------------------------------------------- 
		Homepage 'our services' mobile-only carousel
---------------------------------------------------
 */

(function() {
  define("component/our-services", ["imagesloaded/imagesloaded"], function(ImagesLoaded) {
    var OurServices;
    return OurServices = (function() {
      OurServices.prototype.element = null;

      function OurServices(element) {
        this.element = element;
        new ImagesLoaded(this.element[0], (function(_this) {
          return function() {
            return _this.loadCarousel();
          };
        })(this));
      }

      OurServices.prototype.loadCarousel = function() {
        var slides;
        slides = $('.our-services__mobile', this.element);
        return slides.owlCarousel({
          items: 1,
          loop: true,
          autoplay: true,
          autoplayTimeout: 5000,
          slideBy: 'page',
          nav: false
        });
      };

      return OurServices;

    })();
  });

}).call(this);

