
/*--------------------------------------------------- 
		Homepage 'our clients' carousel
---------------------------------------------------
 */

(function() {
  define("component/our-clients", ["imagesloaded/imagesloaded"], function(ImagesLoaded) {
    var OurClients;
    return OurClients = (function() {
      OurClients.prototype.element = null;

      function OurClients(element) {
        this.element = element;
        new ImagesLoaded(this.element[0], (function(_this) {
          return function() {
            return _this.loadCarousel();
          };
        })(this));
      }

      OurClients.prototype.loadCarousel = function() {
        var slides;
        slides = $('.our-clients__clients', this.element);
        slides.owlCarousel({
          items: 5,
          loop: true,
          autoplay: true,
          autoplayTimeout: 5000,
          slideBy: 'page',
          nav: false,
          responsiveRefreshRate: 0,
          responsive: {
            0: {
              items: 1
            },
            360: {
              items: 2
            },
            450: {
              items: 3
            },
            768: {
              items: 4
            },
            900: {
              items: 5
            }
          },
          onInitialized: (function(_this) {
            return function() {
              return _this.normalizeHeights();
            };
          })(this)
        });
        return $(window).resize((function(_this) {
          return function() {
            return _this.normalizeHeights();
          };
        })(this));
      };

      OurClients.prototype.normalizeHeights = function() {
        var items, maxHeight;
        maxHeight = 0;
        items = $('.owl-item', this.element);
        items.css('height', '');
        items.each(function() {
          var height;
          height = $(this).outerHeight();
          return maxHeight = Math.max(height, $(this).outerHeight());
        });
        return items.height(maxHeight);
      };

      return OurClients;

    })();
  });

}).call(this);

