
/*--------------------------------------------------- 
		Mobile-only page sub-nav
---------------------------------------------------
 */

(function() {
  define("component/mobile-sub-nav", [], function() {
    var MobileSubNav;
    return MobileSubNav = (function() {
      MobileSubNav.prototype.element = null;

      function MobileSubNav(element) {
        this.element = element;
        $('.mobile-sub-nav__parent', this.element).on('click', (function(_this) {
          return function(e) {
            if (_this.navClicked($(e.delegateTarget).attr('href'))) {
              return e.preventDefault();
            }
          };
        })(this));
      }

      MobileSubNav.prototype.navClicked = function(link) {
        var mobileNav;
        mobileNav = this.getMobileNav();
        if (mobileNav.hasLink(link)) {
          mobileNav.navigateTo(link);
          return true;
        }
        return false;
      };

      MobileSubNav.prototype.getMobileNav = function() {
        var mobileNav;
        mobileNav = $('.mobile-nav').get(0);
        return mobileNav.component;
      };

      return MobileSubNav;

    })();
  });

}).call(this);

