
/*--------------------------------------------------- 
		Language selector dropdown
---------------------------------------------------
 */

(function() {
  define("component/languages", [], function() {
    var Languages;
    return Languages = (function() {
      Languages.prototype.element = null;

      function Languages(element) {
        this.element = element;
        this.element.on('click', (function(_this) {
          return function(e) {
            if ($(e.currentTarget).closest('.languages__dropdown').length === 0) {
              return _this.element.toggleClass('languages--open');
            }
          };
        })(this));
      }

      return Languages;

    })();
  });

}).call(this);

