
/*--------------------------------------------------- 
		Generic form container
---------------------------------------------------
 */

(function() {
  define("component/form", [], function() {
    var Form;
    return Form = (function() {
      Form.prototype.element = null;

      function Form(element) {
        this.element = element;
        this.manageInputFills(true);
        $((function(_this) {
          return function() {
            _this.manageInputFills();
            _this.manageValidation();
            return _this.manageSubmit();
          };
        })(this));
      }

      Form.prototype.manageInputFills = function(listenEvents) {
        var dropdowns, textFields;
        if (listenEvents == null) {
          listenEvents = false;
        }
        textFields = $('.form-text, .form-textarea', this.element);
        textFields.each((function(_this) {
          return function(i, val) {
            return _this.checkTextFill($(val));
          };
        })(this));
        if (listenEvents) {
          textFields.on('change', (function(_this) {
            return function(e) {
              _this.checkTextFill($(e.delegateTarget));
              if ($(e.delegateTarget).data('trigger') != null) {
                return _this.checkTriggers($(e.delegateTarget));
              }
            };
          })(this));
        }
        dropdowns = $('.form-dropdown', this.element);
        dropdowns.each((function(_this) {
          return function(i, val) {
            return _this.checkDropdownFill($(val));
          };
        })(this));
        if (listenEvents) {
          return dropdowns.on('change', (function(_this) {
            return function(e) {
              _this.checkDropdownFill($(e.delegateTarget));
              if ($(e.delegateTarget).data('trigger') != null) {
                return _this.checkTriggers($(e.delegateTarget));
              }
            };
          })(this));
        }
      };

      Form.prototype.checkTextFill = function(element) {
        var input;
        input = $('.form-text__input, .form-textarea__text', element);
        if (element.hasClass('form-text')) {
          if (input.val() !== '') {
            return element.addClass('form-text--filled');
          } else {
            return element.removeClass('form-text--filled');
          }
        } else if (element.hasClass('form-textarea')) {
          if (input.val() !== '') {
            return element.addClass('form-textarea--filled');
          } else {
            return element.removeClass('form-textarea--filled');
          }
        }
      };

      Form.prototype.checkDropdownFill = function(element) {
        var select;
        select = $('.form-dropdown__select', element);
        if (select[0].selectedIndex !== 0) {
          return element.addClass('form-dropdown--selected');
        } else {
          return element.removeClass('form-dropdown--selected');
        }
      };

      Form.prototype.manageValidation = function() {
        return $('.form__element', this.element).on('change', (function(_this) {
          return function(e) {
            return _this.validateElement($(e.delegateTarget));
          };
        })(this));
      };

      Form.prototype.validateElement = function(element) {
        var valid, validators;
        valid = true;
        validators = $('.form-error', element);
        validators.each(function(i, val) {
          ValidatorValidate(val);
          if (!val.isvalid) {
            $(val).addClass('form-error--invalid');
          } else {
            $(val).removeClass('form-error--invalid');
          }
          if (!val.isvalid) {
            return valid = false;
          }
        });
        if (!valid) {
          element.addClass('form__element--invalid');
          return element.removeClass('form__element--valid');
        } else {
          element.removeClass('form__element--invalid');
          return element.addClass('form__element--valid');
        }
      };

      Form.prototype.manageSubmit = function() {
        return $('.form__submit-button', this.element).on('click', (function(_this) {
          return function(e) {
            $('.form__element', _this.element).each(function(i, val) {
              return _this.validateElement($(val));
            });
            if (Page.IsPostback) {
              Page.Validate();
              if (!Page.isvalid) {
                return e.preventDefault();
              }
            }
          };
        })(this));
      };

      Form.prototype.checkTriggers = function(element) {
        var numOptions, selectedIndex, triggered, triggers;
        triggered = false;
        triggers = element.data('trigger');
        if (triggers.idx != null) {
          numOptions = $('.form-dropdown__select', element).find('option').length;
          selectedIndex = $('.form-dropdown__select', element).find(':selected').index();
          if ((triggers.idx === -1 && selectedIndex === numOptions - 1) || selectedIndex === triggers.idx) {
            triggered = true;
          }
        }
        if (triggered) {
          return $(triggers.target, this.element).addClass('form-trigger--visible');
        } else {
          return $(triggers.target, this.element).removeClass('form-trigger--visible');
        }
      };

      return Form;

    })();
  });

}).call(this);

