
/*--------------------------------------------------- 
		Homepage 'our solutions' mobile-only carousel
---------------------------------------------------
 */

(function() {
  define("component/our-solutions", ["imagesloaded/imagesloaded"], function(ImagesLoaded) {
    var OurServices;
    return OurServices = (function() {
      OurServices.prototype.element = null;

      function OurServices(element) {
        this.element = element;
        new ImagesLoaded(this.element[0], (function(_this) {
          return function() {
            return _this.loadCarousel();
          };
        })(this));
      }

      OurServices.prototype.loadCarousel = function() {
        var slides;
        slides = $('.our-solutions__mobile', this.element);
        return slides.owlCarousel({
          items: 1,
          loop: true,
          autoHeight: true,
          slideBy: 'page',
          nav: false,
          onResize: function() {
            return this.trigger('changed', {
              property: {
                name: 'position',
                value: this._current
              }
            });
          }
        });
      };

      return OurServices;

    })();
  });

}).call(this);

