
/*--------------------------------------------------- 
		Houses common page functionality
---------------------------------------------------
 */

(function() {
  define("common", ["util/wysiwyg", "util/wait-image", "util/wait-visible", "fastclick/fastclick"], function(Wysiwyg, WaitImage, WaitVisible, FastClick) {
    $('html').removeClass('no-js');
    FastClick.attach(document.body);
    Wysiwyg.parseAll();
    WaitImage.waitAll();
    return WaitVisible.waitAll();
  });

}).call(this);

